<template>
   <!--   -->
   <div class="modal-detail">
    <div @click="openStartDialog" class="close-button">
      <v-icon>mdi-close</v-icon>
    </div>
    <v-card class="custom-card">
      <v-text-field label=" " 
        v-model="tripPlanName" 
        placeholder=" 8   ."
        :rules="tripPlanNameRules"
        class="gray-text-field no-cursor "
        disabled></v-text-field>
      <v-text-field label=" " 
        v-model="tripUserName" 
        placeholder="    ."
        class="gray-text-field no-cursor"
        disabled></v-text-field>
      <v-select
        label=" "
        v-model="tripYear"
        :items="tripYears"
        item-title="string"
        item-value="value"
        class="no-cursor"
      ></v-select>
      <div class="date-fields">
      <v-text-field
        label="  "
        v-model="tripStartDate"
        placeholder="MMDD"  
        :rules="tripStartDateRules"
        class="no-cursor"
      ></v-text-field>
      <v-text-field
        label="  "
        v-model="tripEndDate"
        placeholder="MMDD"  
        :rules="tripEndDateRules"
        class="end-date-field no-cursor"
      ></v-text-field>
      </div>
      <v-checkbox 
        :label="checkboxLabel"
        v-model="showDetailFields"
        class="no-cursor"></v-checkbox>
      <div v-if="showDetailFields" class="detail-fields">
      <v-select
        label="  "
        v-model="tripStartTime"
        :items="tripStartTimes"
        item-title="string"
        item-value="value"
        class="no-cursor"
      ></v-select>
      <v-select
        label="  "
        v-model="tripPlanUnit"
        :items="tripPlanUnits"
        item-title="string"
        item-value="value"
        class="plan-unit-field no-cursor"
      ></v-select>
      </div>
      <v-card-actions>
        <v-btn @click="openStartDialog"></v-btn>
        <v-btn @click="makeTrip" color="primary"> </v-btn>
      </v-card-actions>
      <!-- v-alert  -->
      <v-alert 
        v-model="alert"
        type="warning"
        class="disabled-alert"
        transition="scale-transition"
        closable
         prominent
        >
        {{ alertMessage }}
      </v-alert>
    </v-card>
  </div>
</template>
<script>
export default {
  name: "TripSelectDetail",
  components: {
  
  },
  data(){
    return {
      tripPlanName: "",
      tripUserName: "",
      companionName: "",
      tripYear: new Date().getFullYear(),
      tripYears: [],
      tripStartTime: "0900",
      tripStartTimes: [
        {string: "0000", value: "0000"}, {string: "0100", value: "0100"}, {string: "0200", value: "0200"},
        {string: "0300", value: "0300"}, {string: "0400", value: "0400"}, {string: "0500", value: "0500"},
        {string: "0600", value: "0600"}, {string: "0700", value: "0700"}, {string: "0800", value: "0800"},
        {string: "0900", value: "0900"}, {string: "1000", value: "1000"}, {string: "1100", value: "1100"},
        {string: "1200", value: "1200"}, {string: "1300", value: "1300"}, {string: "1400", value: "1400"},
        {string: "1500", value: "1500"}, {string: "1600", value: "1600"}, {string: "1700", value: "1700"},
        {string: "1800", value: "1800"}, {string: "1900", value: "1900"}, {string: "2000", value: "2000"},
        {string: "2100", value: "2100"}, {string: "2200", value: "2200"}, {string: "2300", value: "2300"},
      ],
      tripPlanUnit: 60,
      tripPlanUnits: [
        {string : "20", value: 20},{string : "30", value: 30},
        {string : "1", value: 60},{string : "2", value: 120},
        {string : "3", value: 180},{string : "4", value: 240},
        {string : "6", value: 360},
      ],
      showDetailFields: false,  // v-checkbox    
      tripPlanNameRules: [
          v => !!v || '   .',
          v => !( v && v.length > 8) || ' 8    .'
      ],
      tripStartDateRules: [
        v => !!v || '    .',
        v => /^[0-9]*$/.test(v) || '     .',
        v => !( v && v.length != 4) || '   4 .',
        v => this.checkDate(v),
      ],
      tripEndDateRules: [
        v => !!v || '    .',
        v => /^[0-9]*$/.test(v) || '     .',
        v => !( v && v.length != 4) || '   4 .',
        v => this.checkDate(v),
      ],
      tripStartDate: "",
      tripEndDate: "",
      checkboxLabel: "     \n (   [09 / 1] )",
      alertMessage: "", // v-alert  
      alert: false,

    }
  },
  filters: {
    nl2br: function (value) {
      return value.replace(/\n/g, '<br>');
    },
  },
  created(){
   
  },
  mounted(){
     this.emitter.on('submitDetail', (data)=>{
        this.tripUserName = data.tripUserName;
        this.tripPlanName = data.tripPlanName;

        console.log("data.tripUserName :: " + data.tripUserName)
        console.log("data.companionName :: " + data.companionName)
     });

    for(let i=0; i < 30; i++){
        let value = 2010+i;
        this.tripYears[i] = {"string" : value+"" , "value" : 2010+i};
    }

  },
  methods: {
    test(){
      this.alert = !alert
    },
    closeDialog() {
      this.$emit("closeDialog");
    },
    openStartDialog() {
      this.$emit("openStartDialog");
    },
    checkDate(date){
      const moment = require('moment');
      const checkFlag = moment(this.tripYear+date, "YYYYMMDD").isValid();

      if(!checkFlag){
        return "  .";
      }
    },
    makeTrip(){
        const moment = require('moment');
        const startDateString = this.tripYear+this.tripStartDate;
        const endDateString = this.tripYear+this.tripEndDate;
        console.log("startDateString : " + startDateString);
        console.log("endDateString : " + endDateString);

        const formattedStartDate = startDateString.substring(0, 4)+"-"+parseInt(startDateString.substring(4, 6))+"-"+ startDateString.substring(6, 8);
        const formattedEndDate = endDateString.substring(0, 4)+"-"+parseInt(endDateString.substring(4, 6))+"-"+ endDateString.substring(6, 8);
        
        const startDateMoment = moment(formattedStartDate);
        const endDateMoment = moment(formattedEndDate);

        const planAllNum = endDateMoment.diff(startDateMoment, 'days')+1;
        const compareFlag = moment(startDateMoment).isBefore(endDateMoment);

        console.log(compareFlag);
        if(!compareFlag){
          this.alertMessage = "     ."
          this.alert = true;
        }else{  
          this.alertMessage = ""; //   
          this.alert = false;
          const tripUser = {
            tripUserName: this.tripUserName,
            tripProjYn: "Y",
          };

        
          const tripPlan = {
            tripPlanNm: this.tripPlanName,
            tripPlannerNm: this.tripUserName,
            tripPlanStDt: formattedStartDate,
            tripPlanEdDt: formattedEndDate,
            tripPlanDefaultYn : "Y",
            tripPlanStTime: this.tripStartTime,
            tripPlanTimeUnit: this.tripPlanUnit,
            tripPlanAllNum: planAllNum,
          }

            console.log(this.tripPlanName);
            console.log(this.tripUserName); 
            console.log(formattedStartDate); 
            console.log(formattedEndDate); 
            console.log(planAllNum); 


          this.$axios.post('/trip/user/info', tripUser)
           .then(userResponse => {
              //    
              console.log(userResponse.data);
              return this.$axios.post(`/trip/plan/info`, tripPlan);
            })
            .then(planResponse => {
               console.log(planResponse.data);
            })
            .catch(error => {
              //      
              console.error(error);
          });

        }
    },
  }
};
</script>

<style scoped>
.modal-detail {
  background-color: white;
  padding: 20px;
  width: 35%;
  position: relative;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 2; /*      */
}

.close-button v-icon {
  font-size: 24px; /*    */
  width: 24px; /*    */
  height: 24px; /*    */
}

.no-cursor {
  caret-color: transparent; /*     */
}

.custom-card {
  margin-top: 20px;
  position: relative;
}

/* v-text-field     */
.v-text-field, .v-select, .v-checkbox{
  margin-bottom: -5px; /*     (  ) */
}

.date-fields, .detail-fields{
  display: flex;
  justify-content: space-between;
}

.end-date-field , .plan-unit-field {
  margin-left: 10px; /*    */
}

.gray-text-field {
  color: black;
}

.v-card-actions {
  justify-content: flex-end;
}

.disabled-alert{
  margin: auto;
  text-align: center;
  width: 50%; 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

}

.scale-transition {
  transition: transform 0.3s;
}
</style>
